/* ********************* page_Stage ***************************** */

.page_stage{
    width: 500px;
    margin: 0px auto 20px;
    position: relative;
    padding-top: 200px; 
}

.page_stage ul{
    display: flex;
    justify-content: space-between;
    text-align: center;
}


.page_stage ul::before{
    content: "";
    position: absolute;
    z-index: -1;
    width: calc(100% - 40px); 
    height: 1px;
    top: 30%;left: 12px;
    background-color: black;
    margin-left: 10px;
    margin-top: 140px;
}

.page_stage ul .num{
    margin: 0 auto;
    text-align: center;
    background-color: white;
    height: 40px;width: 40px;
    border-radius: 50px;
    line-height: 40px;
    font-weight: bold;
    box-shadow: 1px 10px 30px rgba(128, 128, 128, 0.593);
}


/* 로그인 페이지 단계 표시 */
.page_stage ul li:nth-child(1) .num,
.page_stage ul li:nth-child(2) .num{
    background-color: #ccc;
    border: none;
}

.page_stage ul p{
    margin-top: 10px;
}


